@use '/src/style/color/semantic.scss' as *;
@use '/src/style/typography/typography.scss' as *;

.header-wrap {
  width: 100%;
  height: 100px;
  min-height: 100px;
}

.header-container {
  @include headline2-semibold;

  height: 99%;
  display: flex;
  align-items: center;
  justify-content: flex-end;

  gap: 40px;

  padding-right: 100px;

  a {
    color: var(--Static-White);

    &:focus {
      outline: none;
    }
  }
}
